import React from "react";

import DefaultLayout from './DefaultLayout';
import "./css/Bio.css"
import bioJson from "./athletes.json";
const rawBios = bioJson["athletes"];


class Bio extends React.Component {

    constructor(props) {
        super(props);
        this.rawBio = rawBios.find(b => b.id === props.id);
    }

    render() {
        const rawBio = this.rawBio;

        let i = 0;
        let details = [];
        if (rawBio.hometown)
            details.push(<p key={i++}><strong>Hometown:&nbsp;</strong>{rawBio.hometown}</p>);
        
        if (rawBio.occupation)
            details.push(<p key={i++}><strong>Occupation:&nbsp;</strong>{rawBio.occupation}</p>);
        

        if (rawBio.records) {
            const records = rawBio.records.map( ([discipline, time], j) => 
                <li key={j}>
                    <strong>{discipline}:</strong>&nbsp; {time}
                </li>
            );

            details.push(<p key={i++}><strong>Personal Bests</strong></p>);
            details.push(<ul key={i++}>{records}</ul>)
        }

        return (
            <DefaultLayout>
                <h1 className="page-header">
                    {rawBio.name}
                </h1>
                <div className="bio-container">
                    <img className="bio-page-image" alt={rawBio.name} src={rawBio.image}/>
                    <div className="bio-text">
                        {details}
                    </div>
                </div>
           </DefaultLayout>
        );
    }
}

export default Bio;