import DefaultLayout from "./DefaultLayout";

import races_json from "./races.json";

const toRow = function(race, i) {
    var key_val = -1
    var cells = [];
    cells.push( <td key={key_val += 1}>{race.date}</td> );

    if (race.link)
        cells.push( <td key={`toRow_${++key_val}`}>
            <a href={race.link} target="_blank" rel="noreferrer">{race.name}</a>
            </td>
        );
    else
        cells.push( <td key={`toRow_${++key_val}`}>{race.name}</td>);

    return (<tr key={key_val += 1}>{cells}</tr>);
};


function Races(props) {
    const races = races_json.races;

    const rows = races.map(toRow);

    return (
        <DefaultLayout>
            <h1 className="page-header">Races</h1>
            <table>
                <tbody>
                    <tr key={`header_row`}>
                        <th key={`date_header`}>Date</th>
                        <th key={`race_header`}>Race</th>
                    </tr>
                    {rows}
                </tbody>
            </table>
        </DefaultLayout>
    )

}

export default Races;