// 3rd Party
import React from "react";
import DefaultLayout from "./DefaultLayout";
import "./css/News.css";


var FB = null;
// Event is emitted in index.html
document.addEventListener("fb_init", (e) => {
    FB = e.detail;
});

class News extends React.Component {

    render() {
        return (
            <DefaultLayout>
                <h1 className="page-header">
                    News
                </h1>
                <div className="fb-wrapper">
                        <div 
                            className="fb-page"
                            data-width="250px"
                            data-href="https://www.facebook.com/Cahaba-Distance-Project-159904641087413"
                            data-tabs="timeline"
                            data-small-header="true"
                            data-hide-cover="true"
                            data-show-facepile="false">
                        </div>
                </div>

            </DefaultLayout>
        )
    }

    componentDidMount() {
        if (FB) FB.XFBML.parse();
    }
}

export default News;