import React from "react";
import {Link} from "react-router-dom";
import "./css/DefaultLayout.css";

import ew_logo from "./img/ew_logo.jpg";
import trak_shak_logo from "./img/trak_shak_logo.jpg";

class DefaultLayout extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            displayDropdown: false
        }
    }

    getDropdownClasses() {
        if (this.state.displayDropdown)
            return "dropdown thin";
        else
            return "dropdown-hidden thin";
    }

    render() {
      const links = [
        {to: "/contact", name: "Contact"},
        {to: "/roster", name: "Roster"},
        {to: "/news", name: "News"},
        {to: "/races", name: "Races"}
      ]

          const sponsors = [
            {
              name: "EW Motion Therapy",
              img: ew_logo,
              link: "https://ewmotiontherapy.com/"
            },
            {
              name: "The Trak Shak",
              img: trak_shak_logo,
              link: "https://www.trakshak.com/"
            }
        
          ];

        const formatted_sponsors = sponsors.map( (sponsor, i) => {
                  return (
                    <div key={i} className="sponsor-box">
                      <a href={sponsor.link} target="_blank" rel="noreferrer">
                        <img className="sponsor-image hover-transparent"
                            alt={sponsor.name}
                            title={sponsor.name}
                            src={sponsor.img}
                        />
                      </a>
                    </div>
                  );});


      const toggleDropdown = () => {
        this.setState( function(prev) {
            return {displayDropdown: !prev.displayDropdown};
        });
      }

      return (
          <div className="DefaultLayout vertical-flex-container"  >
            <header className="site-header horizontal-flex-container" >

              <div className="cahaba-logo">
                  <h1 className="cahaba-logo-main"><Link to="/">cahaba</Link></h1>
                  <h2 className="cahaba-logo-sub"><Link to="/">Distance Project</Link></h2>
              </div>



              <div className="vertical-end-flex-container menu-container">
                <ul className="horizontal-flex-container menu-list">
                  {
                    links.map( (l,i) => (
                        <li key={i} className="menu-box">
                          <Link className="black-white-link" to={l.to}>
                            {l.name}
                          </Link>
                        </li>
                      )
                    )
                  }
                </ul>
              </div>

              <div onClick={toggleDropdown} className="dropbtn vertical-end-flex-container">
                    &#9776;
              </div>
            </header>

            <div className={this.getDropdownClasses()}>
                  <ul className="dropdown-list thin">
                      {
                          links.map( (l,i) => (
                                <Link key={i} className="dropdown-link" to={l.to}>
                                    <li key={i} className="dropdown-box">
                                        {l.name}
                                    </li>
                                </Link>
                            )
                          )
                      }
                  </ul>
            </div>

            <div className="page-content vertical-flex-container body-text">
              {this.props.children}
            </div>

            <div className="sponsor-bar horizontal-flex-container">
                <div className="vertical-center-flex-container">
                    <div className="sponsor-header">
                        Partners
                    </div>
                </div>
                {formatted_sponsors}
            </div>

          </div>
      );

    }

}

export default DefaultLayout;
